"use client"

import { useState, useEffect } from 'react'
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { useGame } from "@/contexts/GameContext"

export function Market() {
  const [isOpen, setIsOpen] = useState(false)
  const { gameState, updateCoins } = useGame()
  const { player, inventory } = gameState

  // Handle Escape key press
  useEffect(() => {
    const handleEscapeKey = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && isOpen) {
        setIsOpen(false)
      }
    }

    window.addEventListener('keydown', handleEscapeKey)
    return () => window.removeEventListener('keydown', handleEscapeKey)
  }, [isOpen])

  const sellCrop = (itemId: string, price: number) => {
    updateCoins(price)
    // Remove item from inventory logic would be handled by GameContext
  }

  if (!isOpen) {
    return (
      <Button 
        className="w-full text-lg py-3 bg-yellow-600 hover:bg-yellow-700 text-white border-2 border-yellow-500" 
        variant="outline"
        onClick={() => setIsOpen(true)}
      >
        Market
      </Button>
    )
  }

  return (
    <div 
      className="fixed inset-0 bg-black/70 backdrop-blur-sm flex items-center justify-center z-50"
      onClick={(e) => {
        // Close when clicking the backdrop
        if (e.target === e.currentTarget) {
          setIsOpen(false)
        }
      }}
    >
      <Card className="w-[500px] bg-gray-800/95 p-6 border-2 border-gray-600 shadow-2xl relative">
        <Button 
          variant="outline"
          className="absolute top-4 right-4 text-white border-gray-500 hover:bg-gray-700"
          onClick={() => setIsOpen(false)}
        >
          Close
        </Button>

        <div className="space-y-6">
          <h2 className="text-3xl font-bold text-white">Market</h2>

          <div className="space-y-4">
            <h3 className="text-xl font-semibold text-white">Your Crops</h3>
            <div className="space-y-3 max-h-[400px] overflow-y-auto pr-2">
              {inventory.items
                .filter(item => item.type === "crop")
                .map(item => (
                  <div 
                    key={item.id} 
                    className="flex items-center justify-between bg-gray-700/50 p-4 rounded-lg border border-gray-600 hover:border-gray-500 transition-colors"
                  >
                    <div>
                      <p className="text-lg font-medium text-white">{item.name}</p>
                      <p className="text-sm text-gray-300">Quantity: {item.quantity}</p>
                    </div>
                    <Button
                      variant="outline"
                      className="bg-green-600 hover:bg-green-700 text-white border-green-500 ml-4"
                      onClick={() => sellCrop(item.id, item.data.sellPrice)}
                    >
                      Sell for {item.data.sellPrice} ü™ô
                    </Button>
                  </div>
                ))}
              {inventory.items.filter(item => item.type === "crop").length === 0 && (
                <div className="bg-gray-700/30 rounded-lg border border-gray-600 p-8">
                  <p className="text-gray-400 text-center text-lg">
                    No crops to sell yet.<br />
                    Plant some seeds and wait for them to grow!
                  </p>
                </div>
              )}
            </div>
          </div>

          <div className="pt-4 border-t border-gray-600">
            <p className="text-gray-400 text-sm text-center">
              Tip: Press ESC to close this window
            </p>
          </div>
        </div>
      </Card>
    </div>
  )
}
