"use client"

import { createContext, useContext, useState, useEffect, ReactNode } from 'react'
import { SEEDS, TOOLS, LEVEL_THRESHOLDS, type Seed, type Tool } from "../config/items"

interface GameState {
  player: {
    level: number
    experience: number
    energy: number
    coins: number
    position: { x: number; y: number }
    selectedTool: Tool | null
    tools: {
      [key in Tool]: {
        durability?: number
        capacity?: number
      }
    }
  }
  gameTime: {
    hour: number
    minute: number
    day: number
    season: string
  }
  inventory: {
    items: Array<{
      id: string
      type: "seed" | "crop" | "tool"
      name: string
      quantity: number
      data: any
    }>
    maxSlots: number
  }
  crops: {
    [position: string]: {
      type: Seed
      stage: number
      watered: boolean
      plantedDay: number
    } | null
  }
}

interface GameContextType {
  gameState: GameState
  updatePlayerPosition: (x: number, y: number) => void
  updateEnergy: (amount: number) => void
  updateCoins: (amount: number) => void
  addExperience: (amount: number) => void
  selectTool: (tool: Tool | null) => void
  useTool: (position: string) => void
  plantSeed: (position: string, seed: Seed) => void
  waterCrop: (position: string) => void
  harvestCrop: (position: string) => void
  addItem: (item: Omit<GameState["inventory"]["items"][0], "id">) => void
  removeItem: (itemId: string, quantity: number) => void
}

const defaultGameState: GameState = {
  player: {
    level: 1,
    experience: 0,
    energy: 100,
    coins: 500,
    position: { x: 400, y: 300 },
    selectedTool: null,
    tools: {
      hoe: { durability: 100 },
      wateringCan: { capacity: 100 }
    }
  },
  gameTime: {
    hour: 6,
    minute: 0,
    day: 1,
    season: 'Spring'
  },
  inventory: {
    items: [
      {
        id: "parsnip-seed-1",
        type: "seed",
        name: "Parsnip Seeds",
        quantity: 5,
        data: SEEDS.parsnip
      }
    ],
    maxSlots: 20
  },
  crops: {}
}

const GameContext = createContext<GameContextType | undefined>(undefined)

export function GameProvider({ children }: { children: ReactNode }) {
  const [gameState, setGameState] = useState<GameState>(defaultGameState)

  // Update game time
  useEffect(() => {
    const timer = setInterval(() => {
      setGameState(prev => {
        const newMinute = prev.gameTime.minute + 10
        let newHour = prev.gameTime.hour
        let newDay = prev.gameTime.day

        // Update time
        if (newMinute >= 60) {
          newHour++
        }
        if (newHour >= 24) {
          newHour = 0
          newDay++
        }

        // Update crops
        const newCrops = { ...prev.crops }
        Object.entries(newCrops).forEach(([position, crop]) => {
          if (crop && crop.watered) {
            const seedData = SEEDS[crop.type]
            const daysPassed = newDay - crop.plantedDay
            const growthStage = Math.min(3, Math.floor((daysPassed / seedData.growthTime) * 4))
            
            newCrops[position] = {
              ...crop,
              stage: growthStage,
              watered: false // Reset watering for the new day
            }
          }
        })

        return {
          ...prev,
          gameTime: {
            ...prev.gameTime,
            minute: newMinute % 60,
            hour: newHour,
            day: newDay
          },
          crops: newCrops
        }
      })
    }, 1000) // 1 real second = 10 game minutes

    return () => clearInterval(timer)
  }, [])

  const updatePlayerPosition = (x: number, y: number) => {
    setGameState(prev => ({
      ...prev,
      player: {
        ...prev.player,
        position: { x, y }
      }
    }))
  }

  const updateEnergy = (amount: number) => {
    setGameState(prev => ({
      ...prev,
      player: {
        ...prev.player,
        energy: Math.max(0, Math.min(100, prev.player.energy + amount))
      }
    }))
  }

  const updateCoins = (amount: number) => {
    setGameState(prev => ({
      ...prev,
      player: {
        ...prev.player,
        coins: Math.max(0, prev.player.coins + amount)
      }
    }))
  }

  const addExperience = (amount: number) => {
    setGameState(prev => {
      const newExp = prev.player.experience + amount
      let newLevel = prev.player.level
      
      while (newLevel <= LEVEL_THRESHOLDS.length && newExp >= LEVEL_THRESHOLDS[newLevel - 1]) {
        newLevel++
      }

      return {
        ...prev,
        player: {
          ...prev.player,
          level: newLevel,
          experience: newExp
        }
      }
    })
  }

  const selectTool = (tool: Tool | null) => {
    setGameState(prev => ({
      ...prev,
      player: {
        ...prev.player,
        selectedTool: tool
      }
    }))
  }

  const useTool = (position: string) => {
    setGameState(prev => {
      const tool = prev.player.selectedTool
      if (!tool) return prev

      const toolData = TOOLS[tool]
      if (prev.player.energy < toolData.energyCost) return prev

      const newState = { ...prev }
      
      // Update tool durability/capacity
      if ("durability" in prev.player.tools[tool]) {
        newState.player.tools[tool].durability! -= 1
      }
      if ("capacity" in prev.player.tools[tool] && tool === "wateringCan") {
        newState.player.tools[tool].capacity! -= 1
      }

      // Update energy
      newState.player.energy -= toolData.energyCost

      return newState
    })
  }

  const plantSeed = (position: string, seed: Seed) => {
    setGameState(prev => {
      const seedItem = prev.inventory.items.find(
        item => item.type === "seed" && item.name === SEEDS[seed].name
      )
      if (!seedItem || seedItem.quantity <= 0) return prev

      return {
        ...prev,
        crops: {
          ...prev.crops,
          [position]: {
            type: seed,
            stage: 0,
            watered: false,
            plantedDay: prev.gameTime.day
          }
        },
        inventory: {
          ...prev.inventory,
          items: prev.inventory.items.map(item =>
            item.id === seedItem.id
              ? { ...item, quantity: item.quantity - 1 }
              : item
          ).filter(item => item.quantity > 0)
        }
      }
    })
  }

  const waterCrop = (position: string) => {
    setGameState(prev => {
      if (!prev.crops[position]) return prev
      
      return {
        ...prev,
        crops: {
          ...prev.crops,
          [position]: {
            ...prev.crops[position]!,
            watered: true
          }
        }
      }
    })
  }

  const harvestCrop = (position: string) => {
    setGameState(prev => {
      const crop = prev.crops[position]
      if (!crop) return prev

      const seedData = SEEDS[crop.type]
      const harvestedCrop = {
        type: "crop" as const,
        name: seedData.name.replace(" Seeds", ""),
        quantity: 1,
        data: {
          sellPrice: seedData.sellPrice,
          description: `A fresh ${seedData.name.replace(" Seeds", "").toLowerCase()}.`
        }
      }

      const newState = {
        ...prev,
        crops: {
          ...prev.crops,
          [position]: null
        },
        inventory: {
          ...prev.inventory,
          items: [
            ...prev.inventory.items,
            {
              ...harvestedCrop,
              id: `${crop.type}-${Date.now()}`
            }
          ]
        }
      }

      // Add experience for harvesting
      addExperience(seedData.xp)

      return newState
    })
  }

  const addItem = (item: Omit<GameState["inventory"]["items"][0], "id">) => {
    setGameState(prev => {
      const existingItem = prev.inventory.items.find(
        i => i.type === item.type && i.name === item.name
      )

      if (existingItem) {
        return {
          ...prev,
          inventory: {
            ...prev.inventory,
            items: prev.inventory.items.map(i =>
              i.id === existingItem.id
                ? { ...i, quantity: i.quantity + item.quantity }
                : i
            )
          }
        }
      }

      if (prev.inventory.items.length >= prev.inventory.maxSlots) return prev

      return {
        ...prev,
        inventory: {
          ...prev.inventory,
          items: [
            ...prev.inventory.items,
            {
              ...item,
              id: `${item.type}-${Date.now()}`
            }
          ]
        }
      }
    })
  }

  const removeItem = (itemId: string, quantity: number) => {
    setGameState(prev => ({
      ...prev,
      inventory: {
        ...prev.inventory,
        items: prev.inventory.items.map(item =>
          item.id === itemId
            ? { ...item, quantity: item.quantity - quantity }
            : item
        ).filter(item => item.quantity > 0)
      }
    }))
  }

  return (
    <GameContext.Provider value={{
      gameState,
      updatePlayerPosition,
      updateEnergy,
      updateCoins,
      addExperience,
      selectTool,
      useTool,
      plantSeed,
      waterCrop,
      harvestCrop,
      addItem,
      removeItem
    }}>
      {children}
    </GameContext.Provider>
  )
}

export function useGame() {
  const context = useContext(GameContext)
  if (context === undefined) {
    throw new Error('useGame must be used within a GameProvider')
  }
  return context
}
